@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.requests-list-container{width:30%}.request-item{display:flex;flex-direction:row;cursor:pointer;padding-right:1rem}.request-item__active{background-color:#EBEBEB}.request-item__image{background-color:#BDBDBD;border:1px solid #BDBDBD;border-radius:50%;width:40px;height:40px;display:flex;justify-content:center;align-items:center;margin:0.5rem}.request-item__briefcase{color:#FAFAFA;font-size:1.2rem}.request-item__text{margin:0.5rem 0}.request-item__value{color:#828282}.forms-container{width:70%;display:flex;justify-content:center}.form-component{display:flex;flex-direction:column;padding:2rem}.form-component__element{font-family:"Montserrat",sans-serif;max-width:250px}.input{height:25px;padding:10px}.textarea{height:150px;padding:10px}.button{color:#FAFAFA;background-color:#4452B8;cursor:pointer;outline:none;padding:10px 15px;margin-top:1.5rem;border:1px solid #4452B8;border-radius:5px;transition:opacity 250ms}.button:hover,.button:focus{opacity:0.8}.form_hide{display:none}.loading-window,.empty-dataset-window{font-family:"Montserrat",sans-serif;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.loading-window__message{font-size:3rem}.empty-dataset-window__message{font-size:2rem}*{margin:0;padding:0;box-sizing:border-box}.main-window{font-family:"Montserrat",sans-serif;display:flex;flex-direction:row;width:100vw}@media (max-width: 500px){.main-window{flex-direction:column}.requests-list-container{width:100%;display:grid;grid-template-columns:repeat(auto-fill, minmax(150px, 1fr));grid-gap:5px}.forms-container{width:100%}}

